.accordion {
    list-style: none;
    margin: 16px;
    padding: 16px;
    background-color: #009999;
    border-radius: 7px;
    color: #000;
  }
  
  .accordion-item {
    background-color: #fff;
    border-radius: 7px;
  }
  
  .accordion-item:not(:last-child) {
    margin-bottom: 16px;
  }
  
  .accordion-header {
    position: relative;
    width: 100%;
    padding: 20px 50px 20px 20px;
    background-color: #fff;
    border: 0;
    border-radius: 7px;
    font-size: 22px;
    text-align: left;
    cursor: pointer;
    color: #000;
  }
  
  .accordion-collapse {
    height: 0;
    overflow: hidden;
    transition: height 300ms linear;
  }
  
  .accordion-collapse.open {
    height: auto;
  }
  
  .accordion-body {
    padding: 30px;
    background-color: #000;
    border-bottom-left-radius: 7px;
    border-bottom-right-radius: 7px;
    color: #fff;
    font-size: 20px;
  }
  
  .accordion-arrow {
    position: absolute;
    top: 50%;
    right: 20px;
    display: block;
    width: 30px;
    height: 30px;
    transform: translateY(-50%);
    fill: #009999;
    transition: all 250ms linear;
  }
  
  .accordion-arrow.active {
    transform: translateY(-50%) rotate(180deg);
    fill: red;
  }